  #board {
    display: grid;
    grid-template-columns: repeat(5, 15vmin);
    justify-content: center;
    padding-top: 5vmin;
    padding-bottom: 5vmin;
  }
  
  .cell {
    background-color: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 10px;
    font-size: 30px;
    height: 10vmin;
    text-align: center;
    cursor: pointer; /* Add cursor to indicate it's clickable */
  }
  
  /* Add other CSS styles here as necessary */
  